<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section class="p-sm-0 px-5">
    <div class="mb-3 position-relative">
      <img src="@/assets/images/message.png" class="w-50 mx-auto" alt="" />
      <h3
        class="text-primary position-absolute top-50 start-50 translate-middle text-nowrap"
      >
        下列何者為正確答案？
      </h3>
    </div>
    <div class="row row-cols-1 row-cols-md-2 g-4 mb-5">
      <template v-for="(item, index) in choseList" :key="index">
        <div class="col">
          <div class="card h-100">
            <input
              type="radio"
              class="btn-check"
              :id="`btn-check-outlined-${index}`"
              autocomplete="off"
              name="options-outlined"
              v-model="tempSelect"
              :value="item"
              :disabled="isDisabled"
            />
            <label
              class="btn btn-outline-primary border-3 h-100"
              :for="`btn-check-outlined-${index}`"
              ><div class="row g-0">
                <div class="col-3 position-relative">
                  <div
                    class="h-100 d-flex justify-content-center align-items-center h2 border-end"
                    :class="[
                      tempSelect.select === item.select
                        ? 'border-light'
                        : 'border-primary',
                    ]"
                  >
                    {{ item.select }}
                  </div>
                  <div
                    class="position-absolute top-0 start-0 translate-middle"
                    v-if="tempAnswer.select === item.select"
                    :class="[tempAnswer.show ? '' : 'd-none']"
                  >
                    <i
                      class="bi bi-check-circle h2 text-light"
                      v-if="tempAnswer.isAnswer"
                    ></i>
                    <i class="bi bi-x-circle h2 text-heightlight" v-else></i>
                  </div>
                </div>
                <div class="col-9">
                  <div class="card-body">
                    <p class="card-text">
                      This is a wider card with supporting text below as a
                      natural lead-in to additional content. This content is a
                      little bit longer.
                    </p>
                  </div>
                </div>
              </div></label
            >
          </div>
        </div>
      </template>
    </div>
    <div class="d-grid gap-2 col-6 mx-auto">
      <button
        class="btn btn-heightlight text-light btn-lg py-3"
        @click="answerCheck"
        :disabled="!tempSelect"
        v-if="!isDisabled"
      >
        檢查答案
      </button>
      <button class="btn btn-outline-heightlight btn-lg py-3" v-else>
        下一題
      </button>
    </div>
  </section>
  <AnswerModal ref="showMessage" />
</template>

<script>
import choseData from '@/assets/jsons/chose.json';
import AnswerModal from '@/components/AnswerModal.vue';

export default {
  data() {
    return {
      choseList: choseData,
      tempSelect: '',
      tempAnswer: '',
      isDisabled: false,
    };
  },
  components: {
    AnswerModal,
  },
  methods: {
    answerCheck() {
      if (!this.tempSelect.isAnswer) {
        this.choseList.forEach((item) => {
          if (item.isAnswer) {
            this.$refs.showMessage.showModal(item);
          }
        });
      }
      this.isDisabled = true;
      this.tempAnswer = this.tempSelect;
      this.tempAnswer.show = true;
    },
  },
};
</script>
